(function () {
    'use strict';

    angular
        .module('donna', ['ngAnimate', 'ui.router', 'oitozero.ngSweetAlert', 'luegg.directives'])

        .run(function ($rootScope, $state, Auth) {
            $rootScope.$on('$stateChangeStart', function (event, toState) {
                if (!Auth.authorize(toState.data.access)) {
                    event.preventDefault();

                    $state.go('anon.login');
                }
            });
        });

})();

(function () {
    'use strict';

    angular
        .module('donna')
        .factory('User', User);


    function User ($http, API) {
        return {
            getUsersList: function () {
                return $http.post(API.URL + '/api/users');
            },

            destroyUser: function (id) {
                return $http.post(API.URL + '/api/user/destroy/' + id);
            }
        }
    }

})();

(function() {
    'use strict';

    angular
        .module('donna')
        .controller('UserController', UserController);

    /** @ngInject */
    function UserController(User, SweetAlert) {
        var vm = this;

        vm.users = [];

        User.getUsersList().success(function (data) {
            vm.users = data;
        }).error(function (err) {
            SweetAlert.swal(err.error, err.summary);
        });

        vm.destroy = function (id) {
            User.destroyUser(id).success(function () {
                vm.users = _.reject(vm.users, function (user) {
                    return id == user.id;
                });
            }).error(function (err) {
                SweetAlert.swal(err.error, err.summary);
            });
        }
    }

})();
